import VHeader from '@/base/VHeader.vue'
import { getBookById, updateBook } from '@/api'
export default {
  name: 'detail',
  components: {
    VHeader
  },
  created () {
    this.getBook()
  },
  computed: {
    bId () {
      return this.$route.params.id
    }
  },
  watch: {
    $route () {
      // 路由发生改变,重新发送请求获取数据
      this.getBook()
      // 如果是空对象 需要跳转回到列表页
      if (Object.keys(this.book).length > 0) {
        this.$router.push({
          name: 'list'
        })
      }
    }
  },
  data () {
    return {
      msg: '详情页',
      book: {}
    }
  },
  methods: {
    async getBook () {
      this.book = await getBookById(this.bId)
    },
    async update () {
      await updateBook(this.bId, this.book)
      // 修改完成后,跳转页面
      this.$router.push({
        name: 'list'
      })
    }
  }
}



